html, body {
  background:#f6f4ef;
  color:#1b1b1b;
  font-size: 16px;
  line-height: 2;
  font-family: "Century Gothic";
  overflow-x: hidden;
}
.flex {
  display: flex;
}

.container {
  max-width: 1140px;
  box-sizing: border-box;
  margin: 0 auto;
}

.header {
background-color: #43444a;
}
.icon {
  position: absolute;
  background: url(../images/social.png) no-repeat;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.fb { width: 9px; height: 16px; background-position: 0 0; }
.inst { width: 13px; height: 13px; background-position: -17px -13px;}
.tw { width: 15px; height: 12px; background-position: -15px 0; }
.vk {
  width: 16px;
  height: 9px;
  background-position: 0 -17px;
}

.vk-h:hover::after {
  background: url(../../../images/vk-hover.png) no-repeat;
  background-position: center;
}
.fb-h:hover::after {
  background: url(../../../images/facebook.png) no-repeat;
  background-position: center;
}
.tw-h:hover::after {
  background: url(../../../images/twitter.png) no-repeat;
  background-position: center;
}
.inst-h:hover::after {
  background: url(../../../images/instagram.png) no-repeat;
  background-position: center;
}

.social-links {
  margin-top: 10px;
  max-width: 135px;
  border: 1px solid #fff;
  padding: 0;

  &__item {
    position: relative;
    display: inline-block;
    width: 33px;
    height: 35px;

    a {
     display: block;
     width: 33px;
     height: 35px;

     &:hover {
       background-color: #fff;
     }

     &:hover::after {
       position: absolute;
       content: '';
       top: 0;
       bottom: 0;
       left: 0;
       right: 0;
       margin: auto;
       width: 100%;
       height: 100%;
     }
   }

  }
}
.social-links li:not(:last-child) {
  border-right: 1px solid #fff;
}
